<script>
import { Popup,Button } from 'vant';
import axios from 'axios'
export default {
  name:'content',
  components:{
    [Popup.name]:Popup,
    [Button.name]:Button
  },
data() {
    return {
        show:false,
        id:'',
        msg:{author:'',title:'',username:'',imageSrc:[],time:'',content:''},
        comments:[],
        isShow:true
    }
},
methods:{
  back(){
    this.$router.go(-1)
  },
  changeshow(){
    this.isShow = !this.isShow
  }
},
activated(){
console.log(2);
},
created(){
    // console.log(this.$route.params.id);
    this.id = this.$route.params.id
    // console.log(this.id);
    axios({
        method:'post',
        url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_article_detail',
        data: {
            article_id:this.id
        },
    }).then(res =>{
        // console.log(res.data.data);
        this.msg = res.data.data
        console.log(this.msg);
    }),
    axios({
        method:'post',
        url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_comment_list',
        data: {
            article_id:this.id
        },
    }).then(res =>{
        // console.log(res.data.data);
        this.comments = res.data.data
        // console.log(this.comments);
    })
}
}
</script>

